import { useMemo } from "react"
import { OrganizationMembershipResource } from "@clerk/types"
import { Watchlist } from "@rp/common/api-types"
import {
  HubSortableHeadCell,
  HubTableRow,
  InfiniteList,
} from "@rp/react/web/common/composite-components"
import { usePageWithListSorter } from "@rp/react/web/common/hooks"
import { Box, CircularProgress } from "@rp/react/web/mui/core"
import { PublicWatchlistsListItem } from "./PublicWatchlistsListItem"

const COLUMNS_WIDTH = ["calc(100% - 450px)", "250px", "150px", "50px"]
const SORT_OPTIONS = [
  { value: "name", label: "Name" },
  { value: "owner", label: "Owner" },
  { value: "lastUpdated", label: "Last modified" },
]

type Props = {
  data?: Array<Watchlist & { owner: OrganizationMembershipResource | undefined }>
  isLoading?: boolean
}

const PublicWatchlists = ({ data = [], isLoading }: Props) => {
  const { sorterFn, sortBy, direction, onChange } = usePageWithListSorter("publicWatchlists")
  const filteredResults = useMemo(() => data.sort(sorterFn), [data, sorterFn])

  if (isLoading) return <CircularProgress sx={{ mt: 20 }} color="primary" />

  return (
    <Box sx={{ width: 1, mt: 1 }}>
      <HubTableRow
        sx={{
          backgroundColor: "transparent",
          height: 35,
          gap: 0,
          "& > div": { pr: 2 },
          "& > div:last-of-type": { pr: 0 },
        }}
        columnsWidth={COLUMNS_WIDTH}
        columns={[
          {
            node: (
              <HubSortableHeadCell
                label="NAME"
                value="name"
                onChange={onChange}
                sortBy={sortBy}
                direction={direction}
                sortOptions={SORT_OPTIONS}
              />
            ),
          },
          {
            node: (
              <HubSortableHeadCell
                label="OWNER"
                value="ownerUserId"
                onChange={onChange}
                sortBy={sortBy}
                direction={direction}
                sortOptions={SORT_OPTIONS}
              />
            ),
          },
          {
            node: (
              <HubSortableHeadCell
                label="LAST MODIFIED"
                value="lastUpdated"
                onChange={onChange}
                sortBy={sortBy}
                direction={direction}
                sortOptions={SORT_OPTIONS}
                defaultDirection="desc"
              />
            ),
          },
          { node: <>&nbsp;</> },
        ]}
      />
      <InfiniteList
        rootMargin={"0px 0px 300px 0px"}
        initialElements={20}
        items={filteredResults}
        renderItem={item => <PublicWatchlistsListItem item={item} columnsWidth={COLUMNS_WIDTH} />}
      />
    </Box>
  )
}

export { PublicWatchlists }
